@import url(https://fonts.googleapis.com/css2?family=Orbitron:wght@400;500;600;700;800;900&display=swap);
* {
    box-sizing: border-box;
}

body {
  margin: 0;
  padding: 20px;
  height: 100vh;
  width: 100vw;
  font-family: 'Orbitron', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #303030;
  letter-spacing: 3px;
  font-size: 22;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.btn {
    cursor: pointer;
}

@media screen and (max-width: 700px) {
    body {
        padding: 0;
    }
    .container {
        margin: 0;
        padding: 0;
    }
}

.container {
    width: 100%;
    padding: 10px 10px;
    border-radius: 5px;
    background-image: url(../../static/media/brushed-aluminium0.602bd398.jpg);
    background-repeat: no-repeat;
    background-size: cover;
}

@media screen and (max-width: 500px) {
    body {
        background-image: url(../../static/media/rotate-device.0eb0da31.png);
        background-size: contain;
        background-repeat: no-repeat;
        background-color: white;
    }
    .container {
        display: none;
    }
}


.control-panel {
    padding: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    border: 2px solid rgba(0, 0, 0, 0.50);
    border-radius: 8px;
    font-size: 18px;
}
.paused {
    background-color: rgb(129, 172, 43);
    padding-left: 5px;
}
.playing {
    background-color: yellowgreen;
}

.play-pause-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 90px;
    width: 90px;
    border-radius: 50%;
    box-shadow: 0 0 5px black;
}

img {
    height: 80px;
}


.reset-btn {
    text-transform: uppercase;
    background-color: rgba(0, 0, 0, 0.8);
    padding: 10px;
    border-radius: 10px;
    color: yellowgreen;
}

.reset-btn p {
    margin: 0;
    padding: 0;
}

.reset-btn:active {
    transform: scale(.95);
}
#kits {
    background-color: black;
    color: yellowgreen;
    font-size: inherit;
    font-family: inherit;
    letter-spacing: inherit;
    text-transform: uppercase;
    border: none;
}

.presets-menu {
    background-color: black;
    padding: 5px;
    border-radius: 5px;
}


.tempo {
    display: inline-block;
    font-size: inherit;
    background-color: black;
    color: yellowgreen;
    border-radius: 5px;
    text-decoration: none;
    padding: 4px 0;
    min-width: 30px;
    text-align: center;
}

.current-tempo {
    margin: 0;
    background-color: black;
    color: yellowgreen;
    font-size: inherit;
    min-width: 60px;
}

button {
    background: transparent;
    box-shadow: 0px 0px 0px transparent;
    border: 0px solid transparent;
    text-shadow: 0px 0px 0px transparent;
  }
  
  button:hover {
    box-shadow: 0px 0px 0px transparent;
    border: 0px solid transparent;
    text-shadow: 0px 0px 0px transparent;
  }
  
  button:active {
    outline: none;
    border: none;
  }
  
  button:focus {
    outline: 0;
  }
.indicator-panel {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
}

.buffer {
    display: inline-block;
    width: 15%;
}

.indicators {
    padding: 0 5px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 85%;
    margin-top: 10px;
}

.beat-indicator:nth-child(1) {
    height: 16px;
    width: 16px;
}
.beat-indicator:nth-child(4n+5) {
    height: 16px;
    width: 16px;
}

.beat-indicator {
    height: 12px;
    width: 12px;
    background-color: black;
    border-radius: 50%;
    display: inline-block;
}

.active {
    background-color: red;
}
.samples-section {
    font-size: 20px;
    text-align: center;
}

.sample-row {
    background-color: black;
    font-size: inherit;
    display: flex;
    justify-content: space-between;
    border-radius: 10px;
    padding: 5px;
    margin-top: 10px;
}

.quarter {
    display: flex;
    justify-content: space-between;
}
.sample-pad {
    display: inline-block;
    width: 15%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    background-image: url(../../static/media/brushed-aluminium.312149e7.jpg);
    border-radius: 10px;
    font-size: 14px;
    padding: 14px 5px;
    color: whitesmoke;
}

.sample-pad:active {
    transform: scale(.98);
}
:root {
    --first-quarter:  #727272;
    --second-quarter: #4F4F4F;
    --third-quarter: #d2d2be80;
    --fourth-quarter: #bebed260;
    --active: yellowgreen;
    --playing: red;
}

.beat-pad {
    display: inline-block;
    width: 4.8%;
    border-radius: 10%;
    background-color: #bebed260;
    background-color: var(--fourth-quarter);
}

.beat-pad.btn.active {
    background-color: yellowgreen;
    background-color: var(--active);
}

.beat-pad.btn.active.playing {
    background-color: red;
    background-color: var(--playing);
}

/* beat 1 */
.beat-pad:nth-child(-n+5) {
    background-color: #727272;
    background-color: var(--first-quarter);
}

/* beat 2 */
.beat-pad:nth-child(6) {
    background-color: #4F4F4F;
    background-color: var(--second-quarter);
}
.beat-pad:nth-child(7) {
    background-color: #4F4F4F;
    background-color: var(--second-quarter);
}
.beat-pad:nth-child(8) {
    background-color: #4F4F4F;
    background-color: var(--second-quarter);
}
.beat-pad:nth-child(9) {
    background-color: #4F4F4F;
    background-color: var(--second-quarter);
}

/* beat 3 */
.beat-pad:nth-child(10) {
    background-color: #d2d2be80;
    background-color: var(--third-quarter);
}
.beat-pad:nth-child(11) {
    background-color: #d2d2be80;
    background-color: var(--third-quarter);
}
.beat-pad:nth-child(12) {
    background-color: #d2d2be80;
    background-color: var(--third-quarter);
}
.beat-pad:nth-child(13) {
    background-color: #d2d2be80;
    background-color: var(--third-quarter);
}

/* beat 4 */
.beat-pad:nth-last-child(-n+4) {
    background-color: #bebed260;
    background-color: var(--fourth-quarter);
}



